import axios from 'axios';
import { isValidUrl } from './checkURL'; // Make sure this function is defined properly

const input = document.querySelector("form input");
const form = document.querySelector("form");
const error = document.querySelector("#error");
const agreement = document.getElementById("agreement");
const subjectivity = document.getElementById("subjectivity");
const confidence = document.getElementById("confidence");
const irony = document.getElementById("irony");
const score_tag = document.getElementById("score_tag");
const results = document.querySelectorAll("#results div");

document.addEventListener('DOMContentLoaded', () => {
    error.style.display = "none";
});

const handleSubmit = async (event) => {
    event.preventDefault();
    if (!isValidUrl(input.value)) { // Corrected here
        show_error("Enter valid URL?"); // Fixed typo "vaild"
        return;
    }

    const { data } = await axios.post('http://localhost:8000/', form, { // Use `form` correctly
        headers: {
            'Content-Type': 'application/json' // Fixed header key
        }
    });

    const { msg, sample } = data;
    if (msg) {
        show_error(msg);
        return;
    }
    show_results(sample);
};

const show_error = (msg) => {
    error.style.display = "block"; // Fixed typo
    results.forEach(result => {
        result.style.display = "none";
    });
    error.innerHTML = msg;
};

const show_results = (sample) => {
    error.style.display = "none";
    results.forEach(result => {
        result.style.display = "block";
    });
    agreement.innerHTML = `Agreement: ${sample.agreement}`;
    subjectivity.innerHTML = `Subjectivity: ${sample.subjectivity}`;
    confidence.innerHTML = `Confidence: ${sample.confidence}`;
    irony.innerHTML = `Irony: ${sample.irony}`;
    score_tag.innerHTML = `Score Tag: ${sample.score_tag}`;
};

export { handleSubmit };